import * as _ from "lodash";
import * as monaco from "monaco-editor/esm/vs/editor/editor.api";
import { isCCS, isUnderTest } from "../../../services/platform";
import { EditorDiffType } from "../../../services/userPreferences";
import { assertUnreachable } from "../../../services/utils";
import { getHeight } from "../../ComponentUtils";
import { tiGrey } from "../../styles/colors";
import { ThemeType, defaultFontSize } from "../../styles/themes";
import { IEditorProps } from "./IEditor";
import React = require("react");

class Editor extends React.Component<IEditorProps> {

	private scrollAfterRender = false;
	// HTML refs that the editors will attach to.	
	// These elements must be empty.
	private standardEditorNode: HTMLDivElement;
	private diffEditorNode: HTMLDivElement;

	// The editor objects
	private standardEditor: monaco.editor.IStandaloneCodeEditor;
	private diffEditor: monaco.editor.IStandaloneDiffEditor;

	// The text models (the contents)
	private presentModel: monaco.editor.ITextModel;
	private pastModel: monaco.editor.ITextModel;

	private subscription: monaco.IDisposable;

	public override render() {

		// Unfortunately we can't scroll until the div is rendered.  If we do, then it will scroll it right to the very
		// top, which a) hides the diff and b) might scroll most of the text off screen.  However, React gives no 
		// "post-render" event, and monaco gives no "onRender" event, so the only option is to set a variable and
		// scroll in a setImmediate.

		// I'm using a member, and not React state, as setting React state would trigger an extra React render, so 
		// React doesn't even let you set it.  Thus it has to be outside of React, and that's ok because we're updating
		// monaco which is outside of react

		if (this.scrollAfterRender) {
			this.scrollAfterRender = false;
			setTimeout(() => {
				this.scrollToFirstDifference();
			}, 0);
		}

		// Now render the divs that monaco will attach to

		const { hasError, diffType } = this.props;
		return (
			<div>
				<div ref={(divRef) => this.standardEditorNode = divRef} id={"editor"}
					style={(hasError || diffType !== EditorDiffType.NODIFF)
						? { display: "none" } : {}}></div>
				<div ref={(divRef) => this.diffEditorNode = divRef} id={"diff_editor"}
					style={(hasError || diffType === EditorDiffType.NODIFF)
						? { display: "none" } : {}}></div>
			</div>
		);
	}

	public override componentDidMount() {
		const fileType = this.getFileType();

		this.pastModel = monaco.editor.createModel("", fileType);
		this.presentModel = monaco.editor.createModel("", fileType);

		// create diff editor
		this.diffEditor =
			monaco.editor.createDiffEditor(
				this.diffEditorNode,
				{
					automaticLayout: true,
					readOnly: true,
					lineNumbers: isUnderTest() ? "off" : "on",
					renderSideBySide: this.props.diffType === EditorDiffType.SIDEBYSIDE,
					fontSize: defaultFontSize,
				},
			);
		this.diffEditor.setModel({
			original: this.pastModel,
			modified: this.presentModel,
		});
		this.subscription = this.diffEditor.onDidUpdateDiff(this.scrollToFirstDifference);

		monaco.editor.defineTheme("darkTheme", {
			base: "vs-dark",
			inherit: true,
			rules: [],
			colors: {
				"editor.background": tiGrey[800],
				"editor.lineHighlightBackground": "#4D4D4F",
			},
		});

		monaco.editor.defineTheme("lightTheme", {
			base: "vs",
			inherit: true,
			rules: [],
			colors: {
			},
		});

		if (this.props.theme.name === ThemeType.DARK) {
			monaco.editor.setTheme("darkTheme");
		} else if (this.props.theme.name === ThemeType.LIGHT) {
			monaco.editor.setTheme("lightTheme");
		} else {
			assertUnreachable(this.props.theme.name);
		}

		// create standard editor
		this.standardEditor =
			monaco.editor.create(
				this.standardEditorNode,
				{
					automaticLayout: true,
					readOnly: true,
					model: this.presentModel,
					lineNumbers: isUnderTest() ? "off" : "on",
					minimap: {
						enabled: false,
					},
					fontSize: defaultFontSize,
				},
			);

		this.setEditorValue();
		this.reSizeEditor();
		window.addEventListener("resize", this.reSizeEditor);
	}

	// Destroy the editor so we don't leak

	public override componentWillUnmount() {
		this.subscription.dispose();
		this.standardEditor.dispose();
		this.diffEditor.dispose();
		this.pastModel.dispose();
		this.presentModel.dispose();
		window.removeEventListener("resize", this.reSizeEditor);
	}

	public override componentDidUpdate(prevProps: Readonly<IEditorProps>) {
		if (this.propsChanged(prevProps)) {
			this.setEditorValue();
			if (this.props.diffType !== prevProps.diffType) {
				const newOptions: monaco.editor.IDiffEditorOptions
					= { renderSideBySide: this.props.diffType === EditorDiffType.SIDEBYSIDE };
				this.diffEditor.updateOptions(newOptions);

				// We are about to display the diff editor - set state so we scroll AFTER it's rendered.  See render()
				// for more details

				if (prevProps.diffType === EditorDiffType.NODIFF) {
					this.scrollAfterRender = true;
				}
			}
		}
	}

	private propsChanged(prevProps: Readonly<IEditorProps>) {
		return !_.isEqual(this.props, prevProps);
	}

	private reSizeEditor = () => {
		const height = getHeight();
		// adjusting the height of the editor according to the screen size
		this.standardEditorNode.style.height = (height - (isCCS() ? 60 : defaultFontSize * 8.5)) + "px";
		this.diffEditorNode.style.height = (height - (isCCS() ? 60 : defaultFontSize * 8.5)) + "px";
	}

	private setEditorValue() {
		if (!this.props.hasError && this.props.currentContent) {
			if (this.props.previousContent !== this.pastModel.getValue()) {
				this.pastModel.setValue(this.props.previousContent);
			}
			if (this.props.currentContent !== this.presentModel.getValue()) {
				this.presentModel.setValue(this.props.currentContent);
			}
		}
	}

	private getFileType() {
		const fileExt = this.props.file.name.split(".").pop().toLowerCase();
		switch (fileExt) {
			case "js":
			case "xs":	// xs is for ROV RTSC scripts
			case "syscfg":
				return "javascript";
			case "json":
				return "json";
			case "c":
			case "h":
				return "c";
			case "cpp":
			case "hpp":
				return "cpp";
			default:
				return "plaintext";
		}
	}

	private scrollToFirstDifference = () => {
		if (this.props.diffType !== EditorDiffType.NODIFF) {
			const firstDiff = _.first(this.diffEditor.getLineChanges());
			if (firstDiff) {
				this.diffEditor.revealLineInCenterIfOutsideViewport(
					_.min([firstDiff.modifiedStartLineNumber, firstDiff.originalStartLineNumber]),
				);
			}
		}
	}
}

export = Editor;
